import useAxios from '@/hooks/useAxios';
import { Space, Badge, NoticeBar } from 'antd-mobile';
import Axios from 'axios';
import { FC, memo, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './index.module.less';
const List: FC = () => {
  const [state, setstate] = useState(0);
  const nva = useNavigate();
  const { doAxios } = useAxios<any>(
    () => Axios.get<any>('https://csdnxiaofu.huangb.top/iconfont.json'),
    [],
  );
  useEffect(() => {
    doAxios();
  }, [doAxios]);

  return (
    <div>
      {state}
      <Space style={{ '--gap': '24px', marginTop: 100 }}>
        <Badge content="5">
          <div className={styles.box} />
        </Badge>

        <Badge content="">
          <div className={styles.box} />
        </Badge>
        <Badge content="">
          <div className={styles.box} />
        </Badge>
        <Badge>
          <div className={styles.box} />
        </Badge>
      </Space>
      <Space block direction="vertical">
        <NoticeBar content="" color="default" />
        <NoticeBar content="" color="alert" />
        <NoticeBar content="" color="error" />
        <NoticeBar content="" color="info" />
      </Space>
      <button
        onClick={(e) => {
          setstate((state) => state + 1);
        }}>
        
      </button>
      <button
        onClick={() => {
          nva('/other');
        }}>
        oher
      </button>
    </div>
  );
};
export default memo(List);
